<template>
<div id="main">
    <div class="map_wrap">
        <div id="map" style="width:100%;height:180%;position:relative;overflow:hidden;"></div>

        <div id="menu_wrap" class="bg_white">
            <div class="option">
                <div>
                    <v-row>
                        <v-text-field
                            v-model="keyword"
                            label="keyword"
                        ></v-text-field>
                        <v-btn @click="searchPlaces()">
                            검색
                        </v-btn>
                    </v-row>
                </div>
            </div>
            
            <ul id="placesList"></ul>
            <div id="pagination"></div>
        </div>
    </div>
</div>

</template>

<script>
import store from '../store/index'

export default {
    name: 'WriteTravelMap',
    data () {
        return {
            mapContainer :  null,
            mapOption : null,
            map :  null,
            infowindow : null,
            markers: [],
            ps: null,
            keyword : null,
            places : [],
            place : null,
            geocoder : null,

        }
    },
    mounted() {
      if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      
      const script = document.createElement("script");
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=212c3ad52c21a8156fe970aa01882e45";
      document.head.appendChild(script);
      }
    },
    
    methods: {
        initMap() {
            // 마커를 담을 배열입니다
            this.markers = [];

            this.mapContainer = document.getElementById('map'), // 지도를 표시할 div 
                this.mapOption = {
                    center: new kakao.maps.LatLng(37.566826, 126.9786567), // 지도의 중심좌표
                    level: 3 // 지도의 확대 레벨
                };  

            // 지도를 생성합니다    
            this.map = new kakao.maps.Map(this.mapContainer, this.mapOption); 

            // 주소-좌표 변환 객체를 생성합니다
            this.geocoder = new kakao.maps.services.Geocoder();

            // 장소 검색 객체를 생성합니다
            this.ps = new kakao.maps.services.Places();  
            
            // 검색 결과 목록이나 마커를 클릭했을 때 장소명을 표출할 인포윈도우를 생성합니다
            this.infowindow = new kakao.maps.InfoWindow({zIndex:1});

            // 키워드로 장소를 검색합니다
            // this.searchPlaces();
        },
        searchPlaces(){
            // var keyword = document.getElementById('keyword').value;


            if (!this.keyword.replace(/^\s+|\s+$/g, '')) {
                alert('키워드를 입력해주세요!');
                return false;
            }

            // 장소검색 객체를 통해 키워드로 장소검색을 요청합니다
            this.ps.keywordSearch(this.keyword, this.placesSearchCB); 
        },
        placesSearchCB(data, status, pagination){
            if (status === kakao.maps.services.Status.OK) {

                // 정상적으로 검색이 완료됐으면
                // 검색 목록과 마커를 표출합니다
                this.displayPlaces(data);
                

                // 페이지 번호를 표출합니다
                this.displayPagination(pagination);

            } else if (status === kakao.maps.services.Status.ZERO_RESULT) {

                alert('검색 결과가 존재하지 않습니다.');
                return;

            } else if (status === kakao.maps.services.Status.ERROR) {

                alert('검색 결과 중 오류가 발생했습니다.');
                return;

            }
        },
        addressSearchFunc(){
            this.geocoder.addressSearch(this.keyword , (result,status) => {
                // 정상적으로 검색이 완료됐으면 
                if (status === kakao.maps.services.Status.OK) {

                    var coords = new kakao.maps.LatLng(result[0].y, result[0].x);

                    // 결과값으로 받은 위치를 마커로 표시합니다
                    var marker = new kakao.maps.Marker({
                        map: this.map,
                        position: coords
                    });

                    // 인포윈도우로 장소에 대한 설명을 표시합니다
                    var infowindow = new kakao.maps.InfoWindow({
                        content: '<div style="width:150px;text-align:center;padding:6px 0;">우리회사</div>'
                    });
                    infowindow.open(this.map, marker);

                    // 지도의 중심을 결과값으로 받은 위치로 이동시킵니다
                    this.map.setCenter(coords);
                } 
            })
        },
        displayPlaces(places){
            var listEl = document.getElementById('placesList'), 
            menuEl = document.getElementById('menu_wrap'),
            fragment = document.createDocumentFragment(), 
            bounds = new kakao.maps.LatLngBounds(), 
            listStr = '';
            
            // 검색 결과 목록에 추가된 항목들을 제거합니다
            this.removeAllChildNods(listEl);

            this.removePlaces();

            // 지도에 표시되고 있는 마커를 제거합니다
            this.removeMarker();
    
            for ( var i=0; i<places.length; i++ ) {
                this.places.push(places[i])
                
                // 마커를 생성하고 지도에 표시합니다
                var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
                    marker = this.addMarker(placePosition, i), 
                    itemEl = this.getListItem(i, places[i]); // 검색 결과 항목 Element를 생성합니다

                // 검색된 장소 위치를 기준으로 지도 범위를 재설정하기위해
                // LatLngBounds 객체에 좌표를 추가합니다
                bounds.extend(placePosition);

                // 마커와 검색결과 항목에 mouseover 했을때
                // 해당 장소에 인포윈도우에 장소명을 표시합니다
                // mouseout 했을 때는 인포윈도우를 닫습니다
                ((marker, title ,i)=> {
                    kakao.maps.event.addListener(marker, 'mouseover', () => {
                        this.displayInfowindow(marker, title);
                    });

                    kakao.maps.event.addListener(marker, 'mouseout', () =>{
                        this.infowindow.close();
                    });

                    itemEl.onmouseover =   () =>{
                        this.displayInfowindow(marker, title);
                    };

                    itemEl.onmouseout =  () =>{
                        this.infowindow.close();
                    };
                    itemEl.onclick = () => {
                    
                        this.addPlace(i);
                    }
                })(marker, places[i].place_name , i);

                fragment.appendChild(itemEl);
            }

            // 검색결과 항목들을 검색결과 목록 Element에 추가합니다
            listEl.appendChild(fragment);
            menuEl.scrollTop = 0;

            // 검색된 장소 위치를 기준으로 지도 범위를 재설정합니다
            this.map.setBounds(bounds);
        },
        displayPagination(pagination){
            var paginationEl = document.getElementById('pagination'),
            fragment = document.createDocumentFragment(),
            i; 

            // 기존에 추가된 페이지번호를 삭제합니다
            while (paginationEl.hasChildNodes()) {
                paginationEl.removeChild (paginationEl.lastChild);
            }

            for (i=1; i<=pagination.last; i++) {
                var el = document.createElement('a');
                el.href = "#";
                el.innerHTML = i;

                if (i===pagination.current) {
                    el.className = 'on';
                } else {
                    el.onclick = (function(i) {
                        return function() {
                            pagination.gotoPage(i);
                        }
                    })(i);
                }

                fragment.appendChild(el);
            }
            paginationEl.appendChild(fragment);
        },
        getListItem(index, places){
            
            var el = document.createElement('li'),
            itemStr = '<span class="markerbg marker_' + (index+1) + '"></span>' +
                        '<div >' +
                        '   <h5>' + places.place_name + '</h5>';

            if (places.road_address_name) {
                itemStr += '    <span>' + places.road_address_name + '</span>' +
                            '   <span class="jibun gray">' +  places.address_name  + '</span>';
            } else {
                itemStr += '    <span>' +  places.address_name  + '</span>'; 
            }
                        
            itemStr += '  <span class="tel">' + places.phone  + '</span>' +
                        '</div>' + '<button v-on:click="addPlace()">추가</button>' + '<hr>';           

            el.innerHTML = itemStr;
            el.className = 'item';

            return el;
        },
        addMarker(position, idx, title){
            var imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png', // 마커 이미지 url, 스프라이트 이미지를 씁니다
            imageSize = new kakao.maps.Size(36, 37),  // 마커 이미지의 크기
            imgOptions =  {
                spriteSize : new kakao.maps.Size(36, 691), // 스프라이트 이미지의 크기
                spriteOrigin : new kakao.maps.Point(0, (idx*46)+10), // 스프라이트 이미지 중 사용할 영역의 좌상단 좌표
                offset: new kakao.maps.Point(13, 37) // 마커 좌표에 일치시킬 이미지 내에서의 좌표
            },
            markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
                marker = new kakao.maps.Marker({
                position: position, // 마커의 위치
                image: markerImage 
            });

            marker.setMap(this.map); // 지도 위에 마커를 표출합니다
            this.markers.push(marker);  // 배열에 생성된 마커를 추가합니다

            return marker;
        },
        removeMarker(){
            for ( var i = 0; i < this.markers.length; i++ ) {
                this.markers[i].setMap(null);
            }   
            this.markers = [];
        },
        displayInfowindow(marker, title){
            var content = '<div style="padding:5px;z-index:1;">' + title + '</div>';

            this.infowindow.setContent(content);
            this.infowindow.open(this.map, marker);
        },
        removeAllChildNods(el){
            while (el.hasChildNodes()) {
                el.removeChild (el.lastChild);
            }
        },
        addPlace(idx){
            const place = this.places[idx];
            store.commit('SET_WRITETRAVELLIST',place)
        },
        removePlaces(){
            this.places.splice(0,this.places.length)
        }

    },
    
    components: {
    },
  }
</script>

